import React, { Component } from 'react';
import './Nav.css';
import './hamburgers.css';
import { Link } from 'react-router-dom';
import { SocialIcon } from 'react-social-icons';
import '../Homepage/bootstrap.min.css';

export default class Nav extends Component {
  componentDidMount() {
    document.addEventListener('scroll', () => {
      const isTop = window.scrollY < 40;
      if (isTop !== this.state.isTop) {
          this.setState({ isTop })
      }
    });
  }

  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      hamburger: false,
      // overlay: false,
      isTop: true,
    };
  }

  render() {

  // const overlay = (this.state.overlay) ? "active" : "" ;

  return (
    <nav className={`navbar-expand-lg navbar-light d-flex justify-content-between p-0 navbar-fixed bg-white position-fixed w-100 navbar ${this.state.isTop ? '' : 'dropShadow '}`}>
      <div className="d-flex flex-column align-items-start w-100">
          <ul className="topNavbar justify-content-center w-100 py-2 mb-0 pl-0 d-flex">
            <li className="nav-item desktop-only">
                <a className={`nav-link top left position-absolute ${this.state.isTop ? 'mt-3' : 'mt-0'}`} target="blank" href="mailto:sumnerbhandal@gmail.com"> get in touch</a>
            </li>
            <li className="nav-item">
              <Link className={`navbar-brand justify-content-center w-100 text-center ${this.state.isTop ? 'p-3' : 'p-0'}`} to="/">
                <span id="Sumner" className={`logo ${this.state.isTop ? 'down' : 'up'}`} alt="logo" onClick={ this.closeMenu }>
                Lucky For Sum
              </span>
            </Link>
            </li>
            <li className="nav-item">
              <ul className={`topNavbar nav-link top right position-absolute px-0 mb-0 pl-0 d-flex ${this.state.isTop ? 'mt-2' : 'mt-0 py-0'}`}>
                  <li className="nav-item float-left p-0 desktop-only ml-2">
                      <SocialIcon target="new"  url="https://twitter.com/sumnerbhandal" style={{ height: 38, width: 38 }} fgColor="#525252" bgColor="#ffffff" />
                  </li>
                  <li className="nav-item float-left p-0  desktop-only ml-2">
                      <SocialIcon target="new"  url="https://uk.linkedin.com/in/sumnerbhandal" style={{ height: 38, width: 38 }} fgColor="#525252" bgColor="#ffffff" />
                  </li>
                  <li className="nav-item float-left p-0  desktop-only ml-2">
                        <SocialIcon target="new" url="https://dribbble.com/sumnerbhandal" style={{ height: 38, width: 38 }} fgColor="#525252" bgColor="#ffffff" />
                  </li>
                  <li className="nav-item float-left p-0  desktop-only ml-2">
                        <SocialIcon target="new" url="https://codepen.io/sumnerbhandal/" style={{ height: 38, width: 38 }} fgColor="#525252" bgColor="#ffffff" />
                  </li>
              </ul>
            </li>
        </ul>
        <div className="d-flex flex-column align-items-start w-100">
        <ul className="topNavbar snail-trail border justify-content-center w-100 py-2 mb-0 pl-0 d-flex">
            <li className="nav-item">
                <span className="nav-link px-3" to="/portfolio">site undergoing maintenance</span>
            </li>
        </ul>
        </div>
      </div>
    </nav>
  );
  }
}



