import React, { Component } from 'react';
import Nav from './component/Nav/Nav';
import { Route, Switch, HashRouter } from 'react-router-dom';
import Loadable from 'react-loadable';

function Loading() {
  return <div>Loading...</div>;
}

const Section1 = Loadable({
  loader: () => import('./component/Homepage/Section1'),
  loading: Loading,
});
const LoadHeaders = Loadable({
  loader: () => import('./component/Homepage/LoadHeaders'),
  loading: Loading,
});
const CarouselPreview1 = Loadable({
  loader: () => import('./component/Homepage/CarouselPreview1'),
  loading: Loading,
});
const Section2 = Loadable({
  loader: () => import('./component/Homepage/Section2'),
  loading: Loading,
});
const CarouselPreview2 = Loadable({
  loader: () => import('./component/Homepage/CarouselPreview2'),
  loading: Loading,
});



class HomePage extends Component {
  constructor(){
    super();
    this.state = {
     scrollY: null,
     fourComponents: false,
     threeComponents: false,
     twoComponents: false,
   }
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    document.addEventListener('resize', () => {
      var intViewportHeight = window.innerHeight;
      if (intViewportHeight > 920) { //arbitrary amount
        this.setState({
            twoComponents: true
        });
      }
      console.log(intViewportHeight);
    });

    document.addEventListener('scroll', () => {
        if (this.state.scrollY > 1) { //arbitrary amount
          this.setState({
              twoComponents: true
          });
        }
        if (this.state.scrollY > 600) { //arbitrary amount
          this.setState({
              threeComponents: true
          });
        }
        if (this.state.scrollY > 1300) { //arbitrary amount
          this.setState({
              fourComponents: true
          });
        }
      }
    );
  }
  handleScroll = (event) => {
    this.setState({
        scrollY: window.scrollY
    });
  }
  mouseOver = (event) => {
  this.setState({
      twoComponents: true
  });
 }
  render() {
    let additionalComponents = null;
    let additionalComponents2 = null;
    let additionalComponents3 = null;

    if (this.state.fourComponents === true) {
      additionalComponents3 = (
        <div>
          <br />
          <CarouselPreview2 />
        </div>
      );
    }
    if (this.state.threeComponents === true) {
        additionalComponents2 = (
            <Section2 />
        );
      }
      if (this.state.twoComponents === true) {
          additionalComponents = (
            <div>
              <CarouselPreview1 />
              <LoadHeaders />
            </div>
          );
      }
    return (
      <div onMouseOver={this.mouseOver}>
        <Section1 />
      { additionalComponents }
      { additionalComponents2 }
      { additionalComponents3 }
      </div>
    );
  }
}

const PortfolioPage = Loadable({
  loader: () => import('./component/Portfolio/PortfolioPage'),
  loading: Loading,
});

const Project = Loadable({
  loader: () => import('./component/Project/Project'),
  loading: Loading,
});


class App extends Component {
  render() {
    return (
      <HashRouter>
        <div>
          <Nav />
          <Switch>
              <Route exact path="/" component={HomePage} />
              <Route exact path="/portfolio" component={PortfolioPage} />
              <Route path="/projects/:id-:title" render={({match}) => <Project match={match} />} />
          </Switch>
        </div>
      </HashRouter>
    );
  }
}

export default App;



